.log-div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 10px;
}

.logo {
  height: 300px;
  width: 300px;
  padding: 10px 10px;
}
.input {
  padding: 10px 10px;
}

.input-div {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Horizontally center */
  align-items: center;
}
.input-box {
  font-size: 20px;
  font-weight: bold;
  color: rgb(11, 87, 80);
}

input {
  font-size: 20px;
  font-weight: bold;
}

button {
  font-size: 20px;
  cursor: pointer;
  margin: auto;
  padding: 5px 5px;
  border: 2px solid rgb(49, 143, 250);
  border-radius: 5px;
}
